        mastery: teenagerInfo.value.monitoring_info?.mastery?.text,
        character: teenagerInfo.value.monitoring_info?.character?.text,
      });

    teenagerInfo.value.employment_info &&
      (teenagerInfo.value.employment_info = {
        ...teenagerInfo.value.employment_info,
        employment_education_type: teenagerInfo.value.employment_info?.employment_education_type?.text,
      });

    console.log(teenagerInfo.value);
    informationTabs.value = informationTabs.value
      .map((n) => {
        return teenagerInfo.value[n.id] ? n : null;
      })
      .filter((n) => n);
  } catch (e) {
    alertThisError(e);
  }
  if (teenagerInfo.value) {
    if (teenagerInfo.value?.parent?.length) {
      teenParents.isNotEmpty = true;
      teenParents.father = teenagerInfo.value.parent.find((e) => {
        return e.parent_type === 'FATHER';
      });
      teenParents.mother = teenagerInfo.value.parent.find((e) => {
        return e.parent_type === 'MOTHER';
