<template>
  <div class="flex flex-col w-full">
    <template v-for="(n, i) in structure" :key="i">
      <div v-if="findValue(n.value)" :key="i" :class="class_wrapper || 'info_wrapper'">
        <span class="info_wrapper_label">
          {{ n.name }}
        </span>
        <p class="info_wrapper_text" v-html="n.ownFunct ? n.ownFunct(findValue(n.value)) : findValue(n.value)"></p>
      </div>
    </template>
  </div>
</template>

<script setup>
import {defineProps} from 'vue';
const props = defineProps(['structure', 'data', 'class_wrapper']);

const findValue = (key) => {
  const itterbaleKey = key.split('.');
  if (itterbaleKey.length > 1) {
    let result = props.data[itterbaleKey[0]];
    for (let i = 1; i < itterbaleKey.length; i++) {
      result = result[itterbaleKey[i]];
    }
    if (result) {
      return result;
    } else {
      return null;
    }
  } else {
    if (key) {
      let result = props.data[key];
      if (result) {
        return result;
      } else {
        return null;
      }
    } else {
      return props.data;
    }
  }
  // const
};
</script>

